___CSS_LOADER_EXPORT___.push([module.id, `.board {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 8px;
  width: fit-content;
}

.cell {
  border: 1px solid #000;
  width: 50px;
  height: 50px;
}

.cell--hidden {
  &:not(.cell--ship, .cell--hit, .cell--missed):hover {
    cursor: pointer;

    &:hover {
      border-color: #a1efb3;
      background-color: #a1efb3;
    }
  }
}

.cell--hit,
.cell--missed {
  display: grid;
  place-content: center;

  &::after {
    content: 'X';
    font-size: 1.5rem;
  }
}

.cell--hit {
  background-color: #e2364a;
}

.cell--missed {
  background-color: #ccc;
}

.cell--ship {
  background-color: #000;
}

.cell--winner {
  border: 2px solid #ff00bf;
}
